<template>
	<div class="wrapper">
		<div class="content">
			<div class="w-left">
				<ctitle :title="'区域来信信访量排行'" class="w-l-title"></ctitle>
				<div class="echarts">
					<horizontal-bar :params="xfbmBarParams"></horizontal-bar>
				</div>
			</div>
			<div class="w-right">
				<div class="r-top">
					<ctitle :title="'来信信访量分布'"></ctitle>
					<div class="ct-content">
						<div class="count">
							<div class="title">全省来信信访量</div>
							<allCount class="all-count" :count="allcount"></allCount>
						</div>
						<div class="detail">
							<detailList :params="detailList"></detailList>
						</div>
						<div class="loop">
								<loopPie
								class="echart"
								:params="zfxszbPieParams"
							></loopPie>
						</div>
						<div class="ring">
							<ring :params="zbRingParams" class="echart"></ring>
						</div>
        	</div>
				</div>
				<div class="r-bottom">
					<ctitle :title="'来信信访量走势'"></ctitle>
          <div class="inner">
						<timeChange class="timeChange" @change="zsTimeClick"></timeChange>
            <LinesR :params="xflzsLineParams"></LinesR>
          </div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import {get_http} from "@/api/index";
import horizontalBar from "@/components/bar/HorizontalBar";
import ctitle from "@/components/title/index";
import allCount from "@/components/list/allCount";
import detailList from "@/components/list/detailList";
import loopPie from "@/components/pie/LoopPie";
import ring from "@/components/pie/ring";
import LinesR from "@/components/line/LinesR";
import timeChange from "@/components/timeChange/index";
	export default {
		name: 'lx',
		components: {
			horizontalBar,
			ctitle,
			allCount,
			detailList,
			loopPie,
			ring,
			LinesR,
			timeChange
		},
		data() {
			return {
				detailList: {},
				// 信访形式占比
        allcount: -1,
				// 1.区域来信信访量排行
				xfbmBarParams: {
					data: [],
					canvasStyle: {
						width: "100%",
						height: "100%"
					}
				},
				// 重件占比(圆环)
        zbRingParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 165,
            radius: ['47%', '58%'],
            bgRadius: ['50%', '51%'],
            center: ["50%", "50%"],
            colors: [
              ["#fee89f", "#eea570"]
            ],
          }
				},
				// 来信信访量初件重件联名件走势
				xflzsLineParams: {
					data: [],
					colors: [
						// 紫
						{
							line: "rgba(14,149,210, 1)",
							start: "rgba(14,149,210, 0.5)",
							end: "rgba(14,149,210, 0.5)"
						},
						{
							line: "rgba(19,240,255, 1)",
							start: "rgba(19,240,255, 0.5)",
							end: "rgba(19,240,255, 0.5)"
						},
						{
							line: "rgba(255,200,51, 1)",
							start: "rgba(255,200,51, 0.5)",
							end: "rgba(255,200,51, 0.5)"
						}
					],
					option: {
						lineWidth: '0',
						legendWidth: 10,
						legendHeight: 10,
						YaxisLineShow: true,
						splitNumber: 4,
						splitLineShow: false,
						legendShow: true,
						legenditemWidth: 22,
						legenditemHeight: 4,
						axisLabelShow: true,
						axisTickShow: false,
						// axisLabelColor: '#82d8ff',
						axisCategoryName: "月",
						// axisYName: "件次",
						axisLabelSize: 14,
						scales: true,
						axisLabelFontSize: 16,
						yaxisLabelFontSize: 14,
						Tofixed: false,
						smooth: false,
						areaShow: 1,
						smoothMonotone: "none",
						dataZoomshow: false,
						legendIcon: "rect",
						left: "5%",
						right: "10%",
						top: "15%",
						bottom: "10%",
						legendTop: "2%",
						legendRight: "10%"
					},
					canvasStyle: {
						width: "100%",
						height: "100%"
					},
					map: {
					},
					xName: "name"
				},
				// 来信信访量信访初件重件联名件分布(loopPie)
				zfxszbPieParams: {
					canvasStyle: {
						width: "100%",
						height: "100%"
					},
					data: [],
					options: {
						bgSize: 220,
						colors: [ ['#6f67ff','#5eceff'], ['#fca82a', '#f5e350']],
						radius: ["39%", "47%"],
						center: ["50%", "50%"],
						centerRadius: ["40%", "41%"],
						labelRich: {
							b: this.fontSize(0.18),
							d: this.fontSize(0.2),
							bHeight: this.fontSize(0.3)
						},
						length: 20,
						length2: 70,
						// borderColor: "#050f28",
						itemLabelShow: true, //数据扇区label是否显示
						//图例
						legend: {
							show: false
						},
						tooltip: {
							show: false
						},
						rich: {
							bfontsize: "16%" //文字大小
						},
						labelSize: "24%", // label字号
						rollFlag: true,
						threeLevel: true
					}
				}
			}
		},
		mounted() {
			this.getAPI(this.date)
		},
		methods: {
			// 来信信访量初件重件联名件走势
			getXflzsLineAPI(data, time) {
				let timetype = "",
					_year = "";
				if (time === "年") {
					timetype = "cy";
					_year = "[now-4, now]";
				} else {
					timetype = "cm";
					_year = data;
				}
				get_http("xftstjfxXfxsLxLmzs", {
					day: `[${this.date.startDate},${this.date.endDate}]`,
				})
					.then(res => {
						this.xflzsLineParams.option.axisCategoryName =
							time === "年" ? "年" : "月";
						this.xflzsLineParams.map = res.data.map;
						this.xflzsLineParams.data = res.data.data;
						// this.zsLineParams.option.axisCategoryName =
            // time === "年度" ? "年" : "月";
          	// this.zsLineParams.map = res.data.map;
          	// this.zsLineParams.data = res.data.data;
					})
					.catch(err => {
						console.log(err);
					});
			},
			getAPI(date) {
				// 1.区域来信信访量排行（左侧柱图）
        get_http("xftstjfxXfxsLxXfllb", {
					day: `[${date.startDate},${date.endDate}]`
				})
				.then(res => {
					this.xfbmBarParams.data = res.data.data.sort((a, b) => {return a.value - b.value;});
				})
				.catch(err => {
					console.log(err);
				});
				// 2.来信信访量信访初件重件联名件分布(总数)
        get_http("xftstjfxXfxsLxLmfbTotal", {
          day: `[${date.startDate},${date.endDate}]`
        })
				.then(res => {
					this.allcount = res.data.data[0].value;
				})
				.catch(err => {
					console.log(err);
				});
				// 3.来信信访量信访初件重件联名件分布(列表)
        get_http("xftstjfxXfxsLxLmfbList", {
          day: `[${date.startDate},${date.endDate}]`
        })
				.then(res => {
					this.detailList = res.data;
				})
				.catch(err => {
					console.log(err);
				});

				// 4.来信信访量信访初件重件联名件分布(loopPie)
				get_http("xftstjfxXfxsLxLmfbLoopPie", {
					day: `[${date.startDate},${date.endDate}]`,
				})
				.then(res => {
					this.zfxszbPieParams.data = res.data.data;
				})
				.catch(err => {
					console.log(err);
				});

				// 5.来信信访量信访初件重件联名件分布(圆环)
        get_http("xftstjfxXfxsLxLmfbRing", {
          day: `[${date.startDate},${date.endDate}]`,
        })
        .then(res => {
          let obj = {};
            let valueKey = 0, percentKey = 0;
            for(let key  in res.data.map){
              if(res.data.map[key]==='件次'){
                valueKey=key
              }
              if(res.data.map[key]==='占比'){
                percentKey=key
              }
            }
            obj.name = res.data.data[0].name;
            obj.value=res.data.data[0][valueKey];
            obj.percent=res.data.data[0][percentKey];
            this.zbRingParams.data = [obj];
        })
        .catch(err => {
          console.log(err);
				});
				

				this.getXflzsLineAPI(date, '年')
			},
			fontSize(res) {
				let docEl = document.documentElement,
					clientWidth =
						window.innerWidth ||
						document.documentElement.clientWidth ||
						document.body.clientWidth;
				if (!clientWidth) return;
				let fontSize = 100 * (clientWidth / 1920);
				return res * fontSize;
			},
			// 时间切换
			zsTimeClick(time) {
        this.getXflzsLineAPI(this.date, time);
      },
		},
		computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val, oval) {
        if (oval.startDate || oval.endDate) {
          this.getAPI(val);
        }
      },
    }
	}
</script>

<style lang="stylus" scoped>
.wrapper
	position relative
	top 0.8rem
	height calc(100% - 0.8rem)
	.content
		width 100%
		height 100%
		display flex
		padding .2rem .3rem
		box-sizing border-box
		display flex
		.w-left
			width 4.5rem
			height 100%
			position relative
			display flex
			flex-direction column
			.echarts
				// height calc(100% - 0.28rem)
				flex 1
				box-sizing border-box
				border-right 1px #91e3f7 dashed
		.w-right
			flex 1
			display flex
			flex-direction column
			.r-top
				flex 1
				height 100%
				.ct-content
					display flex
					align-items center
					height calc(100% - 0.28rem)
					.count
						flex 1
						position relative
						.title
							font-size 18px
							text-align center
							position absolute
							top -38px
							left 79px
							color #fff
							/* font-weight bold */
					.detail
						flex 1
					.loop
						flex 1
						width 100%
						height 100%
					.ring
						flex 1
						width 100%
						height 100%
			.r-bottom
				flex 1
				display flex
				flex-direction column
				position relative
				.inner
					flex 1
					.timeChange
						position absolute
						left 390px
						top 5px
</style>